html,
body {
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}
.icon {
  width: 50px;
  height: 50px;
}
.turbo {
  position: relative;
  width: 100%;
  overflow: hidden;
}
.turbo-track {
  display: flex;
  height: 100%;
}
.turbo-item {
  height: 100%;
  margin: 0 5px;
  overflow: hidden;
  flex-shrink: 0;
}
.turbo-next,
.turbo-back {
  position: absolute;
  z-index: 999;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  fill: #fff;
}
.turbo-next:hover,
.turbo-back:hover {
  color: #56c6f4;
  fill: #56c6f4;
}
.turbo-next {
  right: 0;
}
.turbo-back {
  left: 0;
}
